.home {
  min-height: calc(100vh - 140px);
}
.home__text-mobile {
  text-align: center;
  margin-top: 50px;
}
.home__text-mobile__text {
  color: #aaa;
  font-weight: bold;
  font-size: 18px;
  margin-top: 20px;
}
.home__carousel-wrapper {
  margin-top: 40px;
  margin-bottom: 30px;
  width: 100%;
  height: 200px;
  background-color: rgba(0, 0, 0, 0.6);
  overflow: hidden;
  display: flex;
}
@keyframes go-to-left {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-1920px);
  }
}
@keyframes go-to-right {
  from {
    transform: translateX(-1920px);
  }
  to {
    transform: translateX(0);
  }
}
.home__carousel-wrapper__carousel {
  display: flex;
  align-items: center;
  width: 3840px;
  height: 100%;
  margin-left: 10px;
}
.home__carousel-wrapper__carousel__image {
  height: 180px;
  width: 300px;
  border-radius: 20px;
  margin: 0 10px;
}
.home__carousel-wrapper__carousel--animate-left {
  animation: go-to-left 20s linear infinite;
}
.home__carousel-wrapper__carousel--animate-right {
  animation: go-to-right 20s linear infinite;
}
.home__carousel-wrapper--hidden {
  display: none;
}
.home__start-btn {
  text-align: center;
}
.home__text-desktop {
  display: none;
}
.home__saved-mobile {
  text-align: center;
  margin-top: 20px;
}
.home__saved-mobile__text {
  font-size: 20px;
  color: #aaa;
  margin-bottom: 20px;
}

@media only screen and (min-width: 1000px) {
  .home {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .home__text-mobile {
    display: none;
  }
  .home__text-desktop {
    display: block;
    color: #aaa;
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    width: 400px;
  }
  .home__saved-mobile {
    display: none;
  }
  .home__carousel-wrapper {
    margin-bottom: 60px;
  }
  .home__carousel-wrapper--hidden {
    display: block;
  }
  .home__start-btn {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 60px 100px;
  }
}
