.search {
  text-align: center;
  position: relative;
  min-height: calc(100vh - 160px);
}
.search__form {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 80%;
  margin: 0 auto;
  margin-top: 20px;
}
.search__form__input {
  height: 100%;
  outline: none;
  border: none;
  padding: 10px 20px;
  border-radius: 10px 0 0 10px;
  width: 100%;
  font-size: 16px;
}
.search__form__btn {
  height: 100%;
}
.search__text {
  color: #aaa;
  font-size: 16px;
  margin-top: 10px;
}
.search__loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 25px;
  border-radius: 50%;
  box-shadow: 48px 0 0 0 rgba(255, 255, 255, 0.3), 36px 36px 0 0 rgba(255, 255, 255, 0.3), 0px 48px 0 0 rgba(255, 255, 255, 0.3), -36px 36px 0 0 rgba(255, 255, 255, 0.3), -48px 0 0 0 rgba(255, 255, 255, 0.3), -36px -36px 0 0 rgba(255, 255, 255, 0.3), 0px -48px 0 0 rgba(255, 255, 255, 0.3), 36px -36px 0 0 rgba(255, 255, 255, 0.3);
  animation: rotate 1.5s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.search__photos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 20px;
  padding-bottom: 100px;
}
.search__photos__image {
  border-radius: 20px;
  margin-top: 20px;
  width: 100%;
}
.search__photos__heart {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
  background-color: transparent;
  height: fit-content;
  border: none;
}
.search__photos__heart--liked {
  fill: red;
}
.search__pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.search__pagination__btn {
  margin: 0 10px;
}

@media only screen and (min-width: 1000px) {
  .search__btn-mobile {
    display: none;
  }
