.favorites {
  text-align: center;
  position: relative;
  min-height: calc(100vh - 170px);
}
.favorites__flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.favorites__filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 50px;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  height: 100%;
}
.favorites__filter__input {
  height: 100%;
  outline: none;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  width: 100%;
  font-size: 16px;
}
.favorites__filter__order {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
.favorites__filter__order__text {
  color: #aaa;
  font-size: 18px;
  margin-right: 10px;
}
.favorites__filter__order button {
  margin: 5px;
}
.favorites__text {
  color: #aaa;
  font-size: 16px;
}
.favorites__photos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 20px;
  padding-bottom: 100px;
  margin-top: 20px;
}
.favorites__photos__image {
  border-radius: 20px;
  margin-top: 20px;
  width: 100%;
}
.favorites__photos__trash {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: #252850;
  border-radius: 50%;
  padding: 10px 12px;
  border: none;
  cursor: pointer;
}
.favorites__photos__download {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background-color: #252850;
  border-radius: 50%;
  padding: 10px 12px;
  border: none;
  cursor: pointer;
}
.favorites__photos__edit {
  position: absolute;
  bottom: 70px;
  right: 10px;
  background-color: #252850;
  border-radius: 50%;
  padding: 10px 12px;
  border: none;
  cursor: pointer;
}
.favorites__no-photos {
  text-align: center;
}
.favorites__no-photos__title {
  font-size: 36px;
  color: white;
  text-transform: uppercase;
}
.favorites__no-photos__text {
  text-transform: uppercase;
  margin-top: 20px;
  color: #aaa;
}
.favorites__modal-wrapper {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  z-index: 5;
}
.favorites__modal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  border: 2px solid white;
  background-color: #2E3264;
  width: 80%;
  border-radius: 20px;
  z-index: 10;
  padding: 40px;
}
.favorites__modal__title {
  font-size: 36px;
  color: white;
  margin-bottom: 10px;
}
.favorites__modal__edit {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.favorites__modal__edit__input {
  border: none;
  border-radius: 10px;
  padding: 10px 15px;
  outline: none;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 15px;
}
.favorites__modal__props {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}
.favorites__modal__props__text {
  font-size: 18px;
  color: #aaa;
  margin-bottom: 10px;
}
.favorites__modal__cross {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@media only screen and (min-width: 1000px) {
  .favorites__btn-mobile {
    display: none;
  }
  .favorites__photos {
    display: block;
    margin: 0 40px;
    margin-top: 50px;
    column-count: 3;
    column-gap: 20px;
  }
  .favorites__photos__image {
    display: block;
    border-radius: 20px;
    width: 100%;
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
  }
  .favorites__filter {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 60px;
  }
  .favorites__filter__input {
    margin: 0;
  }
  .favorites__filter__order {
    margin: 0;
    margin-left: 5vw;
    flex-wrap: nowrap;
  }
  .favorites__modal {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
    width: 50%;
    padding: 50px 20px;
  }
  .favorites__modal__props {
    margin-top: 0;
  }
  .favorites__modal__cross {
    top: 20px;
    right: 20px;
  }
}
